import {inject} from 'aurelia-framework';
import Fixtures from './fixtures';
import {LoginStatus} from './messages';
import {EventAggregator} from 'aurelia-event-aggregator';
import AsyncHttpClient from './async-http-client';

@inject(Fixtures, EventAggregator, AsyncHttpClient)
export default class TweetService{
    
    users = [];
    tweets = [];
    userTweets = [];
    loggedInUser = null;

    constructor(data, ea, ac){
        this.ea = ea;
        this.ac = ac;
        this.getTweets();
        this.getUsers();
    }

    getTweets(){
        this.ac.get('/api/tweets').then(res => {
            this.tweets = res.content;
        });
    }

    getUsers(){
        this.ac.get('/api/users').then(res => {
            this.users = res.content;
        });
    }

    getUserTweets(){
        this.ac.get('/api/tweetsUser/' + this.loggedInUser._id).then(res=> {
            this.userTweets = res.content;
        });
    }

    login(email, password){
        const status = {
            success: false,
            message: ''
        };
        
        for(let user of this.users){
            if(user.email === email && user.password === password){
                status.success = true;
                status.message = 'logged in';
                this.loggedInUser = user;
                this.getUserTweets();
            }
        }
        this.ea.publish(new LoginStatus(status));
    }

    register(firstName, lastName, userName, email, password){
        const newUser = {
            firstName: firstName,
            lastName: lastName,
            userName: userName,
            description: "Hi, I'm new to Tweets and this is my profile",
            email: email,
            password: password,
            profileImage: "data:image/png;base64,",
            follows: []
        }

        this.ac.post('/api/users', newUser).then(res => {
            this.getUsers();
        });
    }

    postTweet(text){
        let date = new Date();
        let formattedDate = date.toDateString();
        let tweet = {
            author: null,
            text: text,
            date: date,
            formattedDate: formattedDate,
            postedImage: ""
        };

        this.ac.post('/api/tweets', {tweet: tweet, user: this.loggedInUser}).then(res => {
            const returnedTweet = res.content;
            this.tweets.push(returnedTweet);
        });
    }

    deleteTweet(id){
        this.ac.delete('/api/tweets/' + id).then(res => {
            this.getTweets();
            this.getUserTweets();
        });
    }

    logout(){
        const status = {
            success: false,
            message: ''
        };
        this.loggedInUser = null;
        this.userTweets = null;
        this.ea.publish(new LoginStatus(status));
    }
      
}